@use './variables' as v;

h1, h2, h3 {
    margin: 0 0 v.$separacion 0;
    text-align: center;
    line-height: 1.5;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
    padding-top: v.$separacion;

    span {
        color: v.$secundario ;
        font-size: 1.8rem;
        display: block;
    }

    // &::after {
    //     content: '';
    //     width: 10rem;
    //     height: 10rem;
    //     display: block;
    //     margin: 0 auto;
    //     background-image: url('../img/cafe.svg');
    //     background-size: 10rem;
    // }
}

.heading-blanco {
    @extend h2;
    color: v.$blanco;
    
    span {
        color: v.$blanco;
    }

    &::after {
        display: none;
    }
}

h3 {
    font-size: 3.2rem;
}