@use '../base/variables' as v;
@use '../base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    width: 100%;
    @include m.desktop { 
          flex-direction: row;
          width: auto;
    }
    &__link {
        font-size: 2rem;
        font-weight: 700;
        color: v.$negro;

        &--registrar {
            @include m.boton(v.$primario, v.$blanco)  
        }
    }

}